@import "./mixins.scss";

.anm1 {
    animation: anim1 1s ease-in;
    animation-delay: 4s;
    color: goldenrod;
}

.anm2 {
    margin: 0px 10px;
    animation: anim2 2s ease-in;
    animation-delay: 4s;
    color: goldenrod;
}

.active {
    background-color: rgb(17 24 39);
    color: white;

    &:hover {
        text-decoration: none;
    }
}

