.carousel-inner {
  height: 92vh;
}

.visually-hidden {
  display: none;
}

@media only screen and (max-width: 768px) {
  .carousel-inner {
    height: 100%;
  }
}

.carousel-control-next-icon:hover {
  transition: transform 0.3s ease;
  transform: scale(1.2);
}

.carousel-control-prev-icon:hover {
  transition: transform 0.3s ease;
  transform: scale(1.2);
}